<template>
    <div id="app">
        <button @click="test">test</button>
        {{ res }}
        <img alt="Vue logo" src="./assets/logo.png">
        <HelloWorld msg="Welcome to Your Vue.js App"/>
    </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import {axiosInstance} from "../network/config";

export default {
    name: 'App',
    components: {
        HelloWorld
    },
    data() {
        return {
            res: ""
        }
    },
    methods: {
        test() {
            axiosInstance.get("http://api.pingcc.cn/fiction/search/title/斗罗大陆/1/10")
                .then((res) => {
                    this.res = res
                })
        }
    }
}
</script>

